<template>
  <div class="height">
    <van-nav-bar
      title="监测任务"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="search" slot="right" /> -->
    </van-nav-bar>
    <div class="backPadding">
      <div class="content border-radius">
        <div class="taskCar">
          <img src="static/img/taskCar.png" alt />
        </div>
        <div class="contIn">
          <div class="flexContainer">
            <div class="imgSize">
              <img src="static/img/car.png" alt />
            </div>
            <div class="inputBtn" @click="SelectCarId" id="selectCar">
              <input type="text" class="input" placeholder="选择当前车辆编号" disabled v-model="Car" />
            </div>
          </div>
          <div class="flexContainer">
            <div class="imgSize">
              <img src="static/img/carControl.png" alt />
            </div>
            <div class="inputBtn">
              <input type="text" class="input" placeholder="选择当前人员" disabled v-model="user" />
            </div>
          </div>

          <div class="btnSubmit">
            <div class="borderCircle" @click="Submit">
              <span>开始任务</span>
            </div>
          </div>
          <div class="backBtnSubmit" @click="back">
            <div class="backTo">
              <span>返回当前任务</span>
            </div>
            <div class="imageSized">
              <img src="static/img/toBack.png" alt />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
const h = document.documentElement.clientHeight || document.body.clientHeight;
// const height = Math.floor((h - 300 - 56 - 20 - 32) / 42);
const height = "height:" + h + "px";
import AppBace from "../../../static/ajax";
import Vue from "vue";
import { Toast } from "vant";
Vue.use(Toast);
import { Notify } from "vant";
import { Dialog } from "vant";
// Vue.use(Notify);
export default {
  data() {
    return {
      user: "",
      height: height,
      Car: "",
      token: "",
      time: ""
    };
  },
  async created() {
    this.user = sessionStorage.getItem("user");
    this.token = sessionStorage.getItem("token");
    await this.checkStatus();
  },
  mounted() {
    // console.log(this.$store.state.taskStatus);
  },
  methods: {
    queryTaskStatus() {
      var that = this;
      if (this.$store.state.taskStatus === 0) {
        return null;
      } else {
        // this.$store.state.taskStatus = 1;
        Dialog.confirm({
          title: "任务执行中...",
          message: "是否返回到当前任务？",
          cancelButtonText: "取消",
          confirmButtonText: "确认"
        })
          .then(() => {
            this.$router.push({
              path: "/TaskList",
              query: {
                id: this.$store.state.routingName,
                carId: this.$store.state.vehicleIdStatus,
                time: this.$store.state.time
              }
            });
          })
          .catch(() => {
            // on cancel
            // that.DeleteData(this.$store.state.routingName);
          });
        this.initData(this.$store.state.routingName);
        console.log(this.$store.state.onlineTask);
      }
    },
    async checkStatus() {
      var url = "/mobile/routing/check";
      var res = await AppBace.request(url, "GET", {}, this.token);
      console.log(res.data.status);

      if (res.data.status === "true") {
        this.$store.state.routingName = res.data.data.routingName;
        this.$store.state.vehicleIdStatus = res.data.data.vehicleId;
        this.time = res.data.data.gmtModified;
        this.$store.state.taskStatus = 1;
      } else {
        this.$store.state.taskStatus = 0;
      }
      await this.queryTaskStatus();
    },
    back() {
      if (this.$store.state.taskStatus === 1) {
        this.$router.push({
          path: "/TaskList",
          query: {
            id: this.$store.state.routingName,
            carId: this.$store.state.vehicleIdStatus,
            time: this.$store.state.time
          }
        });
        return;
      }
      Toast("您还没有创建任务！");
    },
    async DeleteData(routingName) {
      var url = "/mobile/routing";
      var res = await AppBace.Deletedata(
        url,
        "DELETE",
        { routingName },
        this.token
      );
      console.log(res);
    },
    SelectCarId() {
      var _this = this;
      var pickerView = new PickerView({
        bindElem: selectCar, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.$store.state.vehicleList, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择站点", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.Car = selectArr[0];
          // var name = Echarts.switchData(selectArr[0]);
          // _this.initData(name);
          // console.log(JSON.stringify(selectArr))
        }
      });
    },
    async Submit() {
      if (this.$store.state.taskStatus === 1) {
        Toast("您有一条任务正在执行，请返回当前任务！");
      } else {
        // this.$router.push('/TaskList');
        var _this = this;
        var url = "/mobile/routing";
        var person = this.user;
        // var taskId = this.$route.query.id;
        var vehicleId = this.Car;
        this.time = AppBace.filterTime(new Date());
        // var res = await AppBace.request(url, "POST", { vehicleId }, this.token);
        this.$http
          .post(url, {
            vehicleId: vehicleId
          })
          .then(res => {
            if (res.data.message === "success") {
              // Toast("创建点位成功");
              _this.$store.state.routingName = res.data.routingName;
              this.$store.state.vehicleIdStatus = vehicleId;
              this.$store.state.time = this.time;
              this.$store.state.taskId = res.data.id;
              Toast({
                message: "正在出车中...",
                icon: "static/img/carControlSelect.png"
              });
              Notify({
                type: "warning",
                message: "车辆准备出库，请谨慎驾驶车辆，遵守交规行驶，谢谢！",
                duration: 5000
              });
              _this.$router.push({
                path: "/TaskList",
                query: {
                  id: res.data.routingName,
                  carId: vehicleId,
                  time: _this.time
                }
              });
            }
            console.log(res);
            _this.$store.state.taskStatus = 0;
          })
          .catch(err => {
            console.log(err);
            Toast("创建路线失败");
          });
      }
    },
    async initData(e) {
      var url = "/mobile/routing/app";
      var res = await AppBace.request(url, "get", {}, this.token);
      console.log(res);
      this.$store.commit(
        "onlinetaskData",
        await this.filterData(res.data.data, e)
      );
    },
    filterData(res, e) {
      for (let index = 0; index < res.length; index++) {
        var data = res[index].routingName;
        if (e === data) {
          console.log(res[index]);
          return res[index];
        }
      }
    }
  }
};
</script>
<style scoped>
.flexContainer {
  display: flex;
  flex-direction: row;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 5px;
  margin-bottom: 10px;
}
.inputBtn {
  flex: 1;
  height: 30px;
}
.imgSize img {
  width: 24px;
  height: 24px;
}
.imgSize {
  display: flex;
  justify-content: center;
  align-items: center;
}
.input {
  width: 100%;
  border-radius: 5px;
  border: none;
  height: 30px;
  color: rgb(119, 119, 119);
  padding: 5px 10px;
  background-color: white;
}
.content {
  padding: 40px;
  background-color: white;
  /* border-radius: 10px; */
}
.height {
  /* padding: 10px; */
}
.backBtnSubmit {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}
.taskCar img {
  width: 100%;
}
.taskCar {
  position: relative;
  /* top: 27px; */
}
.contIn {
  margin-top: 40px;
}
.back {
  background: #ccc;
  margin-top: 10px;
}
.borderCircle {
  height: 120px;
  width: 120px;
  /* background: #24d7c1; */
  background: linear-gradient(#337dff, #23b9ff);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
}
.btnSubmit {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 40px;
  color: white;
}
.backTo {
  border-bottom: #3082fb 1px solid;
  color: #3082fb;
}
.imageSized img {
  width: 16px;
  height: 16px;
}

/* transform:rotate(7deg); */
.imageSized {
  display: flex;
  justify-content: center;
  align-items: center;
  /* transform: rotate(180deg); */
}
.backPadding {
  padding: 10px;
}
</style>


